<template>
  <div class="big-table-wrap" ref="tableWrap">
<!--    <div class="search-wrap" ref="searchWrap">
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">BANNER：</span>
            <Input v-model="userAccount" placeholder="BANNER" class="input-width"></Input>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="reSearchInfo" >查询</Button>
          </div>
        </Col>
      </Row>
    </div>-->
    <!--操作按钮区域-->
    <div class="handle-wrap" ref="handleWrap" >
      <div class="handle-btn-wrap">
        <Button type="primary" @click="addInfoWrapShow">新增</Button>
        <Button type="primary" @click="searchInfo" >查询</Button>
      </div>
    </div>
    <!--table表格区域-->
    <div class="table-wrap-style" ref="tableBox">
      <Table  :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns" stripe border></Table>
    </div>
    <div class="page-wrap" ref="pageWrap">
      <div class="right">
        <Page :total="total" :current="page" @on-change="changePage" @on-page-size-change="changeSize" show-total show-sizer :page-size-opts="pageSizeOpts"></Page>
      </div>
    </div>
    <!--详情弹窗-->
    <Modal
      v-model="detailWrap"
      title="详情">
      <div>
        <div class="detail-one-line">
          <div class="detail-label">平台：</div>
          <div class="detail-value">{{currentChoiceDetail.parent_name}}</div>
        </div>
        <div class="detail-one-line">
          <div class="detail-label">轮播图名称：</div>
          <div class="detail-value">{{currentChoiceDetail.child_name}}</div>
        </div>
        <div class="detail-one-line">
          <div class="detail-label">图片地址：</div>
          <div class="detail-value">{{currentChoiceDetail.field_value1}}</div>
        </div>
        <div class="detail-one-line">
          <div class="detail-label">内容标题：</div>
          <div class="detail-value">{{currentChoiceDetail.field_value2}}</div>
        </div>
        <div class="detail-one-line">
          <div class="detail-label">内容地址：</div>
          <div class="detail-value">{{currentChoiceDetail.field_value3}}</div>
        </div>
        <div class="detail-one-line">
          <div class="detail-label">子类码：</div>
          <div class="detail-value">{{currentChoiceDetail.child_value}}</div>
        </div>
        <div class="detail-one-line">
          <div class="detail-label">排序值：</div>
          <div class="detail-value">{{currentChoiceDetail.order_no}}</div>
        </div>
      </div>
      <div slot="footer">
        <Button type="primary"   @click="detailWrap=false">关闭</Button>
      </div>
    </Modal>
    <!--新增弹窗   -->
    <Modal
      v-model="addWrap"
      title="新增">
      <div>
        <div  class="input-item">
          <span class="input-label">轮播图名称：</span>
          <Input v-model="addInfoDetail.bannerTitle" placeholder="轮播图名称" class="input-width"></Input>
        </div>
        <div  class="input-item">
          <span class="input-label">图片地址：</span>
          <Input type="url" v-model="addInfoDetail.bannerAddres" placeholder="图片地址" class="input-width"></Input>
        </div>
        <div  class="input-item">
          <span class="input-label">内容标题：</span>
          <Input v-model="addInfoDetail.bannerContentTitle" placeholder="内容标题" class="input-width"></Input>
        </div>
        <div  class="input-item">
          <span class="input-label">内容地址：</span>
          <Input v-model="addInfoDetail.bannerLink" placeholder="内容地址" class="input-width"></Input>
        </div>
        <div  class="input-item">
          <span class="input-label">子类码：</span>
          <Input v-model="addInfoDetail.bannerChildCode" placeholder="子类码" class="input-width"></Input>
        </div>
        <div  class="input-item">
          <span class="input-label">排序值：</span>
          <Input v-model="addInfoDetail.bannerRange" placeholder="排序值" class="input-width"></Input>
        </div>

        <div  class="input-item">
          <span class="input-label">平台：</span>
          <Select v-model="addInfoDetail.currentType" class="input-width">
            <Option v-for="item in statueList" :value="item.parent_id" :key="item.parent_id" class="input-width">{{ item.child_name }}</Option>
          </Select>
        </div>
      </div>
      <div slot="footer">
        <Button   @click="addWrap=false">取消</Button>
        <Button type="primary"  :loading="loadingAdd" @click="addInfo">确定</Button>
      </div>
    </Modal>
    <!--编辑弹窗-->
    <Modal
      v-model="editWrap"
      title="编辑">
      <div>
        <div  class="input-item">
          <span class="input-label">轮播图名称：</span>
          <Input v-model="currentChoiceDetail.child_name" placeholder="轮播图名称" class="input-width"></Input>
        </div>
        <div  class="input-item">
          <span class="input-label">图片地址：</span>
          <Input type="url" v-model="currentChoiceDetail.field_value1" placeholder="图片地址" class="input-width"></Input>
        </div>
        <div  class="input-item">
          <span class="input-label">内容标题：</span>
          <Input v-model="currentChoiceDetail.field_value2" placeholder="内容标题" class="input-width"></Input>
        </div>
        <div  class="input-item">
          <span class="input-label">内容地址：</span>
          <Input v-model="currentChoiceDetail.field_value3" placeholder="内容地址" class="input-width"></Input>
        </div>
        <div  class="input-item">
          <span class="input-label">子类码：</span>
          <Input v-model="currentChoiceDetail.child_value" placeholder="子类码" class="input-width"></Input>
        </div>
        <div  class="input-item">
          <span class="input-label">排序值：</span>
          <Input v-model="currentChoiceDetail.order_no" placeholder="排序值" class="input-width"></Input>
        </div>
        <div  class="input-item">
          <span class="input-label">平台：</span>
          <Input v-model="currentChoiceDetail.parent_name" disabled placeholder="平台" class="input-width"></Input>
        </div>
      </div>
      <div slot="footer">
        <Button   @click="editWrap=false">取消</Button>
        <Button type="primary"  :loading="loadingEdit" @click="AddEditInfo">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import { tableMethods } from "./../../js/mixintable";
  import daterange from './../components/daterange'
  export default {
    data () {
      return {
        /*搜索区域 条件*/
        loading:false,// table --loading
        detailWrap:false,//详情弹窗
        currentChoiceDetail:{}, //当前选中的信息
        addWrap:false,//新增信息弹窗
        addInfoDetail:{
          bannerTitle:'',// 新增标题
          bannerAddres:'',// 新增地址
          bannerContentTitle:'',// 新增轮播内容标题
          bannerChildCode:'',// 新增子类码
          bannerRange:'',// 新增排序值
          bannerLink:'',// 新增链接
          currentType:'',//新增---ios/android
        },//新增信息的详情
        loadingAdd:false,//遮罩 Model
        editWrap:false,//编辑弹窗
        loadingEdit:false,//编辑弹窗 遮罩 Model
        /*用户的状态*/
        statueList:[
          {
            child_name: '全部',
            parent_id: ''
          },
        ],
        tableData:[],
        /*table表格*/
        tableColumns: [
          {
            title: '轮播图名称',
            align: 'center',
            width:150,
            key:'child_name'
          },
          {
            title: '图片地址',
            key: 'field_value1',
            align: 'center',
            width:250
          },
          {
            title: '内容标题',
            key: 'field_value2',
            align: 'center',
            width:150,
          },
          {
            title: '内容地址',
            key: 'field_value3',
            align: 'center',
            width:150,
          },
          {
            title: '子类码',
            key: 'child_value',
            align: 'center',
            width:100,
          },
          {
            title: '排序值',
            key: 'order_no',
            align: 'center',
            width:100,

          },
          {
            title: '平台（ios/android）',
            align: 'center',
            key: 'parent_name',
            width:150,
          },
          {
            title: '操作',
            key: 'action',
            width: 250,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      console.log(params.index)
                      this.showDetail(params);
                    }
                  }
                }, '查看'),
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      console.log(params.index)
                      this.editInfoShow(params)
                    }
                  }
                }, '编辑'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      console.log(params.index)
                      this.deleteInfo(params)
                    }
                  }
                }, '删除')
              ]);
            }
          }
        ]
      }
    },
    components: {
      daterange
    },
    mixins: [tableMethods],
    mounted() {
      this.$nextTick(function () {
        //获取用户状态
        this.getSelectInfo()
        //查询数据
        // this.searchInfo();
      })
    },
    methods: {
      //查询数据
      searchInfo(){
        let that=this
        this.loading=true;
        let objData={
          pPage:this.page,
          pSize:this.size
        }
        let jsonData = protocol.getJsonParams(protocol.queryBannerList, objData);
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          /* this.$Loading.finish();*/
          let resultData=[]
          /*添加序号*/
          if(data.returnList&&data.returnList.length>0){
            data.returnList.forEach(function (item,index) {
              item.indexs=that.page*that.size+index-(that.size-1)
              resultData.push(item)
            })
          }
          this.tableData = resultData;
          this.total =data.total-0;
        })
      },
      //获取下拉信息  ios 安卓
      getSelectInfo(){
        let jsonData = protocol.getJsonParams(protocol.queryBannerSelect, {});
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.statueList = this.statueList.concat( data.returnList);
        })
      },
      //显示当前选中的积分商详情
      showDetail(params){
        this.detailWrap=true
        this.currentChoiceDetail=Object.assign({},params.row);
      },
      /*新增-------------------------------------*/
      //添加信息，显示弹窗
      addInfoWrapShow(){
        this.addWrap=true;
        this.loadingAdd=false;
        /*初始化数据*/
        this.addInfoDetail={
          bannerTitle:'',// 新增标题
          bannerAddres:'',// 新增地址
          bannerContentTitle:'',// 新增轮播内容标题
          bannerChildCode:'',// 新增子类码
          bannerRange:'',// 新增排序值
          bannerLink:'',// 新增链接
          currentType:'',//新增---ios/android
        }//新增信息的详情
      },
      //添加信息显示弹窗
      addInfo(){
        //判断下拉是否为空
/*        if(this.addInfoDetail.bannerTitle==''){
          this.$Message.info('请填写标题！');
          return;
        }
        if(this.addInfoDetail.bannerAddres==''){
          this.$Message.info('请填写地址！');
          return;
        }
        console.log(this.addInfoDetail.bannerAddres)
        if(!/^(https?:\/\/)?([\da-z\.-]+)\.([a-z\.]{2,6})([\/\w \.-]*)*\/?$/.test(this.addInfoDetail.bannerAddres)){
          this.$Message.info('请填写正确的地址！');
          return;
        }
        if(this.addInfoDetail.bannerLink==''){
          this.$Message.info('请填写链接！');
          return;
        }
        if(this.addInfoDetail.currentType==''){
          this.$Message.info('请选择平台！');
          return;
        }*/
        this.loadingAdd=true;
        var paramers={
          parent_id:this.addInfoDetail.currentType,
          child_name:this.addInfoDetail.bannerTitle,
          field_value1:this.addInfoDetail.bannerAddres,
          field_value2:this.addInfoDetail.bannerContentTitle,
          field_value3:this.addInfoDetail.bannerLink,
          child_value:this.addInfoDetail.bannerChildCode,
          order_no:this.addInfoDetail.bannerRange,
        }
        let jsonData = protocol.getJsonParams(protocol.addBannerInfo, paramers);
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.loadingAdd=false;
          this.addWrap=false;
          this.searchInfo();
        },()=>{this.loadingAdd=false;})
      },

      //取消新增
      cancelAdd(){
      },
      /*删除--------------------------------------*/
      //删除信息提示
      deleteInfo(params){
        this.$Modal.confirm({
          title: '提示',
          content: `确定要进行删除操作吗？`,
          onOk: ()=>{
            this.deltetInfoOk(params)
          }
        })
      },
      //点击确认删除该条信息
      deltetInfoOk(params){
        let jsonData = protocol.getJsonParams(protocol.deleteBannerInfo, {child_id:params.row.child_id});
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.searchInfo();
        })
        //删除信息后重新获取列表
      },
      /*编辑弹窗------------------------*/
      //显示编辑弹窗
      editInfoShow(params){
        this.editWrap=true;
        this.currentChoiceDetail=Object.assign({},params.row);
        //获取历史信息

      },
      //编辑信息上传
      AddEditInfo(){
/*        if(this.currentChoiceDetail.child_name==''){
          this.$Message.info('请填写标题！');
          return;
        }
        if(this.currentChoiceDetail.child_name_field==''){
          this.$Message.info('请填写地址！');
          return;
        }
        console.log(this.currentChoiceDetail.child_name_field)
        if(	!/^(https?:\/\/)?([\da-z\.-]+)\.([a-z\.]{2,6})([\/\w \.-]*)*\/?$/.test(this.currentChoiceDetail.child_name_field)){
          this.$Message.info('请填写正确的地址！');
          return;
        }
        if(this.currentChoiceDetail.field_value==''){
          this.$Message.info('请填写链接！');
          return;
        }
        if(this.currentChoiceDetail.parent_name==''){
          this.$Message.info('请选择平台！');
          return;
        }*/
        this.loadingEdit=true
        var paramers={
          child_id:this.currentChoiceDetail.child_id,
          child_name:this.currentChoiceDetail.child_name,
          field_value1:this.currentChoiceDetail.field_value1,
          field_value2:this.currentChoiceDetail.field_value2,
          field_value3:this.currentChoiceDetail.field_value3,
          child_value:this.currentChoiceDetail.child_value,
          order_no:this.currentChoiceDetail.order_no,
        }
        let jsonData = protocol.getJsonParams(protocol.updateBannerInfo, paramers);
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.loadingEdit=false
          this.editWrap=false;
          this.searchInfo();
        },()=>{this.loadingEdit=false;})
      }
    },
  }
</script>
<style lang="less" scoped>
  @import './../../css/common.css';
  .input-label{
    width:75px;
  }
  .detail-one-line{
    display: flex;
    padding:5px;
  }
  .detail-label{
    width:85px;
    line-height: 30px;
  }
  .detail-value{
    padding:5px 10px;
    box-sizing: border-box;
    border:1px solid #e3e3e3;
    border-radius: 5px;
    min-width:200px;
    text-align: left;
    flex:1;
  }
</style>



